import React from 'react';
// import { connect } from 'react-redux';

import '../../assets/css/Sketches.css'; 
import SketchGrid from '../SketchGrid';
import sketchList from '../SketchList';
  

export default class Sketches extends React.Component
{
  constructor(props)
  {
    super(props);
    this.state = { sketches: sketchList };
  };

  render()
  {
    return ( 
      <div className="Sketch" style={{
          backgroundColor: '#e6e4e4',
          width: window.innerWidth,
          height: window.innerHeight,
          fontSize: 0.7
      }}>
        <SketchGrid sketches={ this.state.sketches } />
        {/* <SketchGrid sketches={ this.props.sketches } /> */}
      </div>
    );
  };
}

