import * as Tone from 'tone';

import soundPath from '../../assets/sounds/francoise_rosen_-_blue_pentagon.wav';

export default class SoundController
{
    constructor()
    {
        Tone.start();
        this.setup();
    }

    setup()
    {
        // let gain = new Tone.Volume(0).toMaster();
        // gain.set('volume', -15);
        this.createSinths();
    };

    createBackgroundSounds()
    {
        let player = new Tone.Player(soundPath).toMaster();
        player.loop = true;
        // player.playbackRate = 0.5;
        player.autostart = true;
        console.log(player);
        // player.start(3);
        //play as soon as the buffer is loaded
    };

    createSinths()
    {
        let chorus = new Tone.Chorus(2.5, 100, 0.5).toMaster();
        let delay = new Tone.PingPongDelay("4n", 0.8).connect(chorus); //.toMaster();

        this.polySynth = new Tone.PolySynth().connect(delay);
        this.polySynth.toMaster();
        this.polySynth.vibratoAmount = 0.1;

        this.duoSynth = new Tone.DuoSynth().connect(delay);
        this.duoSynth.toMaster();
    };

    playNote(synth)
    {
        let notes = ['A1', 'A2', 'A3', 'A4', 'C1', 'C2', 'C3', 'C4', 'B1', 'B2', 'B3', 'B4', 'D1', 'D2', 'D3', 'D4'];
        synth.triggerAttackRelease((0, randomElement(notes)), "16n");
    };

    playOnTrigger()
    {
        //https://github.com/Tonejs/Tone.js/issues/341
        if (Tone.context.state !== 'running') 
        {
            Tone.context.resume();
        }
        this.playNote(this.duoSynth);
        this.playNote(this.polySynth);
    };

    stop()
    {
        // Tone.Transport.stop();
        Tone.stop();
        // if(this.polySynth) this.polySynth.dispose();
        // if(this.duoSynth) this.duoSynth.dispose();
        console.log('stop audiocontext');
    };

    createEffect()
    {
        // let distortion = new Tone.Distortion(0.6)
        // let tremolo = new Tone.Tremolo().start()

        // let polySynth = new Tone.PolySynth(4, Tone.Synth).chain(distortion, tremolo, Tone.Master)

        // window.addEventListener('mousedown', () => { 
	    // polySynth.triggerAttack(['C1', 'C2', 'C3', 'C4']) 
        // })

        // window.addEventListener('mouseup', () => { 
	    // polySynth.triggerRelease(['C1', 'C2', 'C3', 'C4']) 
        // })
    }
};  

/**
   * returns a random element from an array or object
   * @param  {Array|Object} obj
   * @return {object}
   */

function randomElement(obj) 
{
    if (Array.isArray(obj)) 
    {
        return obj[Math.floor(Math.random() * obj.length)];
    } 
    else if (obj instanceof Object) 
    {
        return obj[randomElement(Object.keys[obj])];
    }
    return obj;
};